import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
//Data Service ist dafuer da Nachrichten zwischen Komponenten auszutauschen
@Injectable(
  {providedIn: 'root'}
)
export class DataService {

  private saeuleIDSource = new BehaviorSubject('');
  private barcodeSource = new BehaviorSubject('');
  currentSaeuleID = this.saeuleIDSource.asObservable();
  currentBarcode = this.barcodeSource.asObservable();

  constructor() { }

  changeSaeuleID(saeuleID: string) {
    this.saeuleIDSource.next(saeuleID)
  }
  changeBarcode(barcode:string){
    this.barcodeSource.next(barcode)
  }

}
