import { Component, OnInit } from '@angular/core';
import { DataService } from "../data.service";
import { Subscription } from 'rxjs';


@Component({
  selector: 'app-top-bar',
  templateUrl: './top-bar.component.html',
  styleUrls: ['./top-bar.component.css']
})
export class TopBarComponent implements OnInit {


  smallWidth=false;
  url=document.URL; //Damit man zurück zum Anfang kommt falls man auf die Datenschutz Seite oder Impressum Seite gegangen ist und statt auf zurück das Robotron Logo anklickt


  constructor(private data: DataService) { }

  ngOnInit(): void { //ista oder esta? oder kista?
    if (window.innerWidth <= 375){ //sonst verschiebt sich der Schriftzug bei kleinen Bildschirmen nach unten und soll hiermit vermieden werden
      this.smallWidth=true;
    }

  }

}
