import { Component, OnDestroy, OnInit } from '@angular/core';
import { ModalDismissReasons, NgbActiveModal, NgbModal,NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import Quagga from "@ericblade/quagga2";
import {CommonModule} from '@angular/common';
import { Platform } from '@angular/cdk/platform';
import { DataService } from "../data.service";
import {NgxBarcodeScannerModule} from '../../../projects/ngx-barcode-scanner/src/lib/ngx-barcode-scanner.module';
import { ActivatedRoute,ParamMap } from '@angular/router';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';

@Component({
	selector: 'ngbd-modal-content',
	standalone: true,
  imports:[NgxBarcodeScannerModule,CommonModule],
	template: `
		<div class="modal-header" fullscreen=true>
      <ng-container *ngIf="lichtSchalterEinblenden">
    <button type="button" class="btn btn-primary"  (click)="lichtSchalter()"><img src="assets/graphics/torch.png" width="30"></button>
		</ng-container>
    <h4 class="modal-title">Kamera auf Barcode richten</h4>
			<button type="button" class="btn-close" aria-label="Close" (click)="activeModal.dismiss('Cross click')"></button>
		</div>
		<div class="modal-body"  fullscreen=true>
    <ngx-barcode-scanner [(value)]="value" (valueChange)="onValueChange()"
                       [codes]="['i2of5']" [errorThreshold]="0.1" (exception)="onError($event)"></ngx-barcode-scanner>
		</div>

	`,
  styleUrls:["./modal/modal.css"]
})

export class NgbdModalContent {
  isError = false;
  value!: string;
  nachricht!:string;
  lichtSchalterZustand = false;
  lichtSchalterEinblenden = true;
  iphone = false;
	constructor(
    public activeModal: NgbActiveModal,
    private data: DataService,
    private platform: Platform

    )
  {
    //Da iOS Torch nicht unterstützt wird der Button der Lichtschalter ausgeblendet
    if(this.platform.IOS){
      this.lichtSchalterEinblenden=false;
      this.iphone=true;
    }
  }
  onValueChange(){
    this.data.changeBarcode(this.value);
    console.log(this.value);
    this.activeModal.close();
  }

  onError(error: any) {
    console.error(error);
    this.isError = true;

  }
  lichtSchalter(){
    if (this.lichtSchalterZustand==false){
      Quagga.CameraAccess.enableTorch();
      this.lichtSchalterZustand=true;
    }
    else{
      Quagga.CameraAccess.disableTorch();
      this.lichtSchalterZustand=false;
    }

  }
}


@Component({
  selector: 'app-barcode-formular',
  templateUrl: './barcode-formular.component.html',
  styleUrls: ['./barcode-formular.component.css']
})
export class BarcodeFormularComponent implements OnInit,OnDestroy {
  value!: string;
  isError = false;
  scannerSichtbar = true;
  saeuleID=this.route.snapshot.paramMap.get('id');
  fehlermeldung:string="Bitte Barcode scannen oder eingeben.";
  isPhonePortrait = false;
  constructor(
    private modalService: NgbModal,
    private data: DataService,
    private route: ActivatedRoute,
    private responsive: BreakpointObserver

  )
  {
    this.data.currentBarcode.subscribe(barcode => this.value=barcode);
   }

  ngOnInit(): void {
    this.responsive.observe(Breakpoints.HandsetPortrait)
    .subscribe(result => {

      this.isPhonePortrait = false;

      if (result.matches) {
        this.isPhonePortrait = true;
      }

});
  }
  abschicken(){
    console.log('ID Säule:');
    console.log(this.saeuleID);
    console.log('Barcode:');
    console.log((<HTMLInputElement>document.getElementById("inputBarcodeScanner")).value); //Inhalt des inputfield wird hier abgerufen

  }
  open(){
    const modalRef = this.modalService.open(NgbdModalContent);
		modalRef.componentInstance.name = 'World';


  }
  ngOnDestroy(): void {
    //Um Speicherleck zu verhindern wird beim zerstören der Komponente auch der Service beendet
    this.data.currentBarcode.subscribe(barcode => this.value=barcode).unsubscribe();

  }
}
