import { Component, OnInit } from '@angular/core';
import { ActivatedRoute,ParamMap } from '@angular/router'
import { DataService } from '../data.service';



@Component({
  selector: 'app-saeule',
  templateUrl: './saeule.component.html',
  styleUrls: ['./saeule.component.css']
})
export class SaeuleComponent implements OnInit {
  id =this.route.snapshot.paramMap.get('id'); //Url Parameter id bekommen
  constructor(
    private route: ActivatedRoute,
    private data: DataService
    )
    {

    }

  ngOnInit(): void {

  this.data.changeSaeuleID(String(this.id)); //Id weiter senden mithilfe DataService an Top-Bar Component
  console.log("Abgegriffene ID");
  console.log(this.id);
  }

}
