import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SaeuleComponent } from '../app/saeule/saeule.component';
import { AppComponent } from './app.component';
import {BarcodeFormularComponent} from '../app/barcode-formular/barcode-formular.component'
import { ImpressumComponent } from './impressum/impressum.component';
import { DatenschutzComponent } from './datenschutz/datenschutz.component';


const routes: Routes = [
  { path: ":id", component: BarcodeFormularComponent },
  //{ path: '**',  component: BarcodeFormularComponent },
  { path:'sonstiges/Impressum',component: ImpressumComponent},
  { path:'sonstiges/Datenschutz',component: DatenschutzComponent},
  { path: '',   redirectTo: '0', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    useHash: true,
    relativeLinkResolution: 'corrected',
  }),],
  exports: [RouterModule]
})

export class AppRoutingModule {



}
